export default {
	data() {
		return {
			//城市
			city:'',
			//控制渐变标题栏的参数
			beforeHeaderzIndex: 11,//层级
			afterHeaderzIndex: 10,//层级
			beforeHeaderOpacity: 1,//不透明度
			afterHeaderOpacity: 0,//不透明度
			//是否显示返回按钮



			//轮播主图数据
			swiperList: [
				{ id: 1, img: '/static/img/c1.jpg' },
				{ id: 2, img: '/static/img/c1.jpg' },
				{ id: 3, img: '/static/img/c1.jpg' },
				{ id: 4, img: '/static/img/c1.jpg' }
			],
			//轮播图下标
			currentSwiper: 0,
			anchorlist:[],//导航条锚点
			selectAnchor:0,//选中锚点
			serviceClass: '',//服务弹窗css类，控制开关动画
			specClass: '',//规格弹窗css类，控制开关动画
			shareClass:'',//分享弹窗css类，控制开关动画
			// 商品信息
			goodsData:{
				spec:[
					{time:"小时",price:"4/小时"},
					{time:"天",price:"20/天"},
					{time:"月",price:"500/月"}
				],
				comment:{
					number:102,
					userface:'../../static/img/face.jpg',
					username:'B哥',
					content:'很不错，之前去过很多次，环境很好'
				}
			},
			type:'',
			roomData:{},
			spec:[],
			selectSpec:null,//选中规格
			isKeep:false,//收藏
			//商品描述html
			descriptionStr:''
		};
	},
	onLoad(option) {

		//小程序隐藏返回按钮
		this.showBack = false;

		//option为object类型，会序列化上个页面传递的参数
		// console.log(option); //打印出上个页面传递的参数。
		if(option.srid == undefined) {
			this.type = '1';
			uni.request({
			    url: 'http://zy.eatclub.wang:3000/list/office/detail?id=' + option.officeid,
			    method: 'GET',  
			    data: {},  
			    success: res => {  
					// console.log(res.data);
					this.roomData = res.data.data[0];
					this.spec = [
						{time:"月",price:res.data.data[0].price+'/月'},
						{time:"年",price:res.data.data[0].price*8+'/年'}
					]
			    },  
			    fail: () => {},  
			    complete: () => {}
			});
		} else {
			this.type = '0';
			uni.request({
			    url: 'http://zy.eatclub.wang:3000/list/sr/detail?id=' + option.srid,
			    method: 'GET',  
			    data: {},  
			    success: res => {  
					// console.log(res.data);
					this.roomData = res.data.data[0];
					this.spec = [
						{time:"小时",price:res.data.data[0].price/5+'/小时'},
						{time:"日",price:res.data.data[0].price+'/日'},
						{time:"月",price:res.data.data[0].price*24+'/月'}
					]
			    },  
			    fail: () => {},  
			    complete: () => {}
			});
		}
	},
	onReady(){
		this.calcAnchor();//计算锚点高度，页面数据是ajax加载时，请把此行放在数据渲染完成事件中执行以保证高度计算正确
	},
	onPageScroll(e) {
		//锚点切换
		this.selectAnchor = e.scrollTop>=this.anchorlist[2].top?2:e.scrollTop>=this.anchorlist[1].top?1:0;
		//导航栏渐变
		let tmpY = 375;
		e.scrollTop = e.scrollTop > tmpY ? 375 : e.scrollTop;
		this.afterHeaderOpacity = e.scrollTop * (1 / tmpY);
		this.beforeHeaderOpacity = 1 - this.afterHeaderOpacity;
		//切换层级
		this.beforeHeaderzIndex = e.scrollTop > 0 ? 10 : 11;
		this.afterHeaderzIndex = e.scrollTop > 0 ? 11 : 10;
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom() {
		uni.showToast({ title: '触发上拉加载' });
	},
	mounted () {
		
	},
	methods: {
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		},
		//消息列表
		toMsg(){
			uni.navigateTo({
				url:'../msg/msg'
			})
		},
		// 客服
		toChat(){
			uni.navigateTo({
				url:"../msg/chat/chat?name=客服008"
			})
		},
		// 分享
		share(){
			this.shareClass = 'show';
		},
		hideShare(){
			this.shareClass = 'hide';
			setTimeout(() => {
				this.shareClass = 'none';
			}, 150);
		},
		//收藏
		keep(){
			this.isKeep = this.isKeep?false:true;
			// console.log(this.isKeep);
			if(this.type == '0') {
				if(this.isKeep) {
					uni.request({
					    url: 'http://zy.eatclub.wang:3000/addcollect',
					    method: 'GET',  
					    data: {
							roomid:this.roomData.srid,
							userid:'1',
							type:'0'
						},  
					    success: res => {  
							
					    },  
					    fail: () => {},  
					    complete: () => {}
					});
				} else {
					uni.request({
					    url: 'http://zy.eatclub.wang:3000/delcollect',
					    method: 'GET',  
					    data: {
							roomid:this.roomData.srid,
							userid:'1',
							type:'0'
						},  
					    success: res => {  
							
					    },  
					    fail: () => {},  
					    complete: () => {}
					});
				}
			} else {
				if(this.isKeep) {
					uni.request({
					    url: 'http://zy.eatclub.wang:3000/addcollect',
					    method: 'GET',  
					    data: {
							roomid:this.roomData.officeid,
							userid:'1',
							type:'1'
						},  
					    success: res => {  
							
					    },  
					    fail: () => {},  
					    complete: () => {}
					});
				} else {
					uni.request({
					    url: 'http://zy.eatclub.wang:3000/delcollect',
					    method: 'GET',  
					    data: {
							roomid:this.roomData.officeid,
							userid:'1',
							type:'1'
						},  
					    success: res => {  
							
					    },  
					    fail: () => {},  
					    complete: () => {}
					});
				}
			}
		},
		//立即购买
		buy(){
			if(this.selectSpec==null){
				return this.showSpec(()=>{
					this.toConfirmation();
				});
			}
			this.toConfirmation();
		},
		//商品评论
		toRatings(){
			uni.navigateTo({
				url:'ratings/ratings'
			})
		},
		//跳转确认订单页面
		toConfirmation(){
			let tmpList=[];
			let goods = {id:this.goodsData.id,img:'../../static/img/goods/p1.jpg',name:this.goodsData.name,spec:'规格:'+this.goodsData.spec[this.selectSpec],price:this.goodsData.price,number:this.goodsData.number};
			tmpList.push(goods);
			uni.setStorage({
				key:'order',
				data:tmpList,
				success: () => {
					uni.navigateTo({
						url:'../order/confirmation'
					})
				}
			})
		},
		//跳转评论列表
		showComments(goodsid){
			
		},
		//选择规格
		setSelectSpec(index){
			this.selectSpec = index;
		},
		//减少数量
		sub(){
			if(this.goodsData.number<=1){
				return;
			}
			this.goodsData.number--;
		},
		//增加数量
		add(){
			this.goodsData.number++;
		},
		//跳转锚点
		toAnchor(index){
			this.selectAnchor = index;
			uni.pageScrollTo({scrollTop: this.anchorlist[index].top,duration: 200});
		},
		//计算锚点高度
		calcAnchor(){
			this.anchorlist=[
				{name:'主图',top:0},
				{name:'评价',top:0},
				{name:'详情',top:0}
			]
			let commentsView = uni.createSelectorQuery().select("#comments");
			commentsView.boundingClientRect((data) => {
				let statusbarHeight = 0;
				//APP内还要计算状态栏高度



				let headerHeight = uni.upx2px(100);
				this.anchorlist[1].top = data.top - headerHeight - statusbarHeight;
				this.anchorlist[2].top = data.bottom - headerHeight - statusbarHeight;
				
			}).exec();
		},
		//返回上一页
		back() {
			uni.navigateBack();
		},
		//服务弹窗
		showService() {
			console.log('show');
			this.serviceClass = 'show';
		},
		//关闭服务弹窗
		hideService() {
			this.serviceClass = 'hide';
			setTimeout(() => {
				this.serviceClass = 'none';
			}, 200);
		},
		//规格弹窗
		showSpec(fun) {
			console.log('show');
			this.specClass = 'show';
			this.specCallback = fun;
		},
		specCallback(){
			return;
		},
		//关闭规格弹窗
		hideSpec() {
			this.specClass = 'hide';
			//回调

			this.selectSpec&&this.specCallback&&this.specCallback();
			this.specCallback = false;
			setTimeout(() => {
				this.specClass = 'none';
			}, 200);
		},
		discard() {
			//丢弃
		}
	}
};
